import React, { useState, useEffect } from "react"
import { ThemeProvider } from "styled-components"

// redux things
import { createStore } from "redux"
import { Provider } from "react-redux"
import rootReducer from "reducers"
import { SET_LIGHT, SET_DARK } from "reducers/theme"
import { useSelector, useDispatch } from "react-redux"

import { light, dark } from "assets/theme"

import GlobalStyles from "components/GlobalStyles"

import Header from "./Header"
import Body from "./Body"
import Footer from "./Footer"

const Layout = ({ children }) => {
  const dispatch = useDispatch()

  // global  reducer  
  const themeMode = useSelector(state => state.theme)

  let isSystemDarkMode = null
  if (typeof window !== "undefined") {
    isSystemDarkMode = window.matchMedia("(prefers-color-scheme: dark)").matches
  }

  let localTheme = null
  if (typeof localStorage !== "undefined") {
    localTheme = localStorage.getItem("theme")
  }

  // const [themeMode, setThemeMode] = useState(
  //   localTheme ? localTheme : isSystemDarkMode ? "dark" : "light"
  // )

  const [theme, setTheme] = useState(light)

  useEffect(() => {
    if (themeMode === "light") setTheme(light)
    else setTheme(dark)
  }, [themeMode])

  const toggleTheme = () => {
    const nextTheme = themeMode === "light" ? "dark" : "light"
    dispatch(nextTheme === "light" ? SET_LIGHT : SET_DARK)
    // setThemeMode(nextTheme)
    localStorage.setItem("theme", nextTheme)
  }

  const store = createStore(rootReducer, enhancer)

  return (
    <Provider store={store}>
      <ThemeProvider theme={theme}>
        <GlobalStyles />
        <Header toggleTheme={toggleTheme} />
        <Body>{children}</Body>
        <Footer />
        <button> HI </button>
      </ThemeProvider>
    </Provider>
  )
}

export default Layout
